import path from "path";
import { ServerContext } from "../server";
import { Plugin } from "../plugin";
import { pathExists } from "fs-extra";
import resolve from "resolve";
import { DEFAULT_EXTENSIONS } from "../constants";
import { normalizePath } from "../utils";

/** Ë∑ØÂæÑËß£ÊûêÊèí‰ª∂ */
export function resolvePlugin(): Plugin {
  let serverContext: ServerContext;

  return {
    name: "h-vite:resolve",
    configureServer(s) {
      // ÂÖà‰øùÂ≠òÊúçÂä°Á´Ø‰∏ä‰∏ãÊñá
      serverContext = s;
    },
    async resolveId(id: string, importer?: string) {
      // 1.Â§ÑÁêÜÁªùÂØπË∑ØÂæÑ
      if (path.isAbsolute(id)) {
        if (await pathExists(id)) {
          // ËøîÂõûÁöÑidÂ∞±ÊòØÂÆûÈôÖÊñá‰ª∂ÁöÑË∑ØÂæÑ
          return { id };
        }
        // Âä†‰∏ä root Ë∑ØÂæÑÂâçÁºÄÔºåÂ§ÑÁêÜ/src/main.tsxÁöÑÊÉÖÂÜµ
        id = path.join(serverContext.root, id);
        if (await pathExists(id)) {
          return { id };
        }
      }

      // 2.Áõ∏ÂØπË∑ØÂæÑ
      else if (id.startsWith(".")) {
        if (!importer) {
          throw new Error("fuck");
        }
        // Âà§Êñ≠ÊòØÂê¶ÊúâÂêéÁºÄÂêç
        const hasExtension = path.extname(id).length > 1;
        let resolvedId: string;
        // 2.1 ÂåÖÂê´ÂêéÁºÄ ./App.tsx
        if (hasExtension) {
          resolvedId = normalizePath(
            resolve.sync(id, { basedir: path.dirname(importer) })
          );
          if (await pathExists(resolvedId)) {
            return { id: resolvedId };
          }
        }
        // 2.2 ‰∏çÂåÖÂê´ÂêéÁºÄ ./App
        else {
          // ./App -> ./App.tsx
          for (const extname of DEFAULT_EXTENSIONS) {
            try {
              const withExtension = `${id}${extname}`;
              resolvedId = normalizePath(
                resolve.sync(withExtension, { basedir: path.dirname(importer) })
              );
              if (await pathExists(resolvedId)) {
                return { id: resolvedId };
              }
            } catch (error) {
              continue;
            }
          }
        }
      }
      return null;
    },
  };
}
